.nav-hero {
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-family: "Dongle", sans-serif;
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.753);

  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

#name {
  font-size: 60px;
  transition: 1s ease-in;
}

.nav-comp {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
li {
  width: 100px;
  padding: 2px;
  /* min-width: 60px; */
  transition: 0.5s ease;
  list-style-type: none;
  cursor: pointer;
  font-size: 22px;
}

li a {
  color: inherit;
  text-decoration: none !important;
  -webkit-text-decoration-color: none;
          text-decoration-color: none;
}
li::after {
  display: none;
  content: "/>";
  font-size: 20px;
  width: 20px;
  transition: 0.5s ease-in;
}
li::before {
  display: none;
  font-size: 20px;
  content: "<";
  width: 20px;
  transition: 0.5s ease-in;
}
.nav-comp:nth-child(1) {
  font-size: 1.8rem;
  width: 96px;
  font-weight: bolder;
}
.nav-comp:nth-child(2) {
  /* font-size:1.8rem; */
  margin-left: 50px;
  width: 40%;
  justify-content: space-evenly;
  height: 20px;
  /* font-weight: bolder; */
}

.nav-comp:nth-child(3) {
  /* font-size:1.8rem; */
  margin-left: 40px;
  width: 150px;
  /* font-weight: bolder; */
}
#name:hover {
  cursor: pointer;

  letter-spacing: 4px;
  color: blueviolet;
}
li:hover::after {
  display: inline;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #49ff18,
    0 0 30px #49ff18, 0 0 40px #49ff18, 0 0 55px #49ff18, 0 0 75px #49ff18;

  /* transition: 2s ease-in; */
}
li:hover::before {
  display: inline;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #49ff18,
    0 0 30px #49ff18, 0 0 40px #49ff18, 0 0 55px #49ff18, 0 0 75px #49ff18;
}

li:hover {
  color: rgba(251, 255, 251, 0.986);
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #49ff18,
    0 0 30px #49ff18, 0 0 40px #49ff18, 0 0 55px #49ff18, 0 0 75px #49ff18;
}

#name div:first-child {
  font-weight: lighter;
}
@media (max-width: 700px) {
  .nav-hero {
    align-items: center;
    flex-direction: column;
    grid-row-gap: 20px;
    row-gap: 20px;
  }

  #name {
    width: 40%;
    justify-content: space-evenly;
    font-size: 80px;
  }
  #menu {
    width: 100%;
  }
}

@media (max-width: 500px) {
