.card {
  position: relative;
  width: 400px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-gap: 20px;
  row-gap: 20px;
  padding: 50px;
  text-align: center;
  margin-bottom: 50px;
  background-color: rgba(29, 28, 28, 0.301);
  background-image: linear-gradient(
    to right,
    rgba(29, 28, 28, 0.301),
    rgba(0, 0, 0, 0.979)
  );
  box-shadow: rgb(0, 0, 0) 0px 20px 40px -10px;
}
.content_hero {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: -webkit-max-content;
  grid-template-rows: max-content;
}

.project_list {
  display: flex;
  flex-direction: column;

  align-items: center;
  line-height: 60px;
  justify-content: space-around;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: rgba(0, 0, 255, 0.068);
  border: none;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.project_list::after {
  content: "";
  -webkit-filter: blur(20px);
          filter: blur(20px);
}

.project_list tr th {
  text-align: center;
  font-family: "Source Code Pro", monospace;
  color: green;
  background-color: rgba(24, 11, 11, 0.205);
  width: 80vw;
}

.project_list tr td {
  text-align: center;
  width: 60vw;
  background-color: rgba(255, 0, 119, 0.103);
  border: none;
}

